import React from 'react';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import { withRouter } from "react-router-dom";

import { EnvProp, Headers } from '../services/envProperties';
import './contact.css';

class Contact extends React.Component {

    state = {
        name: '',
        email: '',
        mobile: ''
    };

    nameField = (event) => {
        this.setState({ name: event.target.value });
    }

    emailField = (event) => {
        this.setState({ email: event.target.value });
    }

    mobileField = (event) => {
        this.setState({ mobile: event.target.value });
    }

    submitContact = async () => {
        try {
            let obj = {
                'name': this.state.name,
                'email': this.state.email,
                'mobile': this.state.mobile
            }
            let config = Headers(obj);
            let response = await fetch(EnvProp.baseUrl + '/submitContact', config);
            let data = await response.json();
            alert(data);
        }
        catch (err) {
            console.log(err);
        }
    }
    render() {
        return (
            <div className="homeBody">
                <Grid container xs={12}>
                    <Grid container xs={12} justify="center">
                        <h1>CONTACT</h1>
                    </Grid>
                    <Grid container xs={12} className="sideGap">
                        <p className="contentText">For faster service, please complete this form. We are available to photograph your portrait or event anywhere in the world.</p>
                    </Grid>
                    <Grid container xs={12} className="sideGap">
                        <TextField id="standard-basic" label="Name *" required variant="outlined" onChange={(event) => this.nameField(event)} />
                    </Grid>
                    <Grid container xs={12} className="sideGap">
                        <TextField id="standard-basic" label="Email *" required variant="outlined" onChange={(event) => this.emailField(event)} />
                    </Grid>
                    <Grid container xs={12} className="sideGap">
                        <TextField id="standard-basic" label="Phone Number *" type='number'
                            required variant="outlined" onChange={(event) => this.mobileField(event)} />
                    </Grid>
                    <Grid container xs={12} justify="center">
                        <div className="contactBtn" onClick={() => this.submitContact()}>
                            Submit
                        </div>
                    </Grid>
                </Grid>
            </div>
        )
    }
}

export default withRouter(Contact);